@import "../colors";

main {
  .about-us {
    margin-bottom: 100px;
    .container {
      .about-us_title {
        margin-bottom: 50px;
        h3 {
          position: relative;
          font-size: 18px;
          line-height: 21px;
          text-align: center;
          letter-spacing: 2.30625px;
        }
        h3:after {
          position: absolute;
          content: '';
          display: block;
          width: 50px;
          left: calc(50% - 25px);
          bottom: -15px;
          border-bottom: 3px solid $aquaBlue;
        }
      }
      .about-us_description {
        justify-content: space-between;
        display: flex;
        .description__text {
          width: 400px;
          margin-right: 10px;
          .description__text-title {
            margin-bottom: 30px;
            font-size: 20px;
            font-weight: 700;
            line-height: 23px;
            align-items: flex-end;
            letter-spacing: 0.1875px;
          }
          .description__text-body {
            font-size: 13px;
            line-height: 23px;
            align-items: center;
            letter-spacing: 0.1875px;
          }
        }
        .description__images {
          display: flex;
          gap: 15px;
          width: 50%;
          .description__images-container {
            position: relative;
            width: 33.3%;
            height: 224px;
            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
            .description__images-text {
              position: absolute;
              top: 20px;
              left: 10px;
              font-size: 24px;
              line-height: 29px;
              font-weight: 700;
              color: $white;
              align-items: center;
              letter-spacing: 0.1875px;
            }
          }
        }
      }
    }
  }
}